const code1 = `server {
  listen 80;

  server_name yoursite.ru;

  gzip on;
  gzip_disable "msie6";
  gzip_types text/plain text/css application/json application/x-javascript text/xml application/xml application/xml+rss text/javascript application/javascript;

  root /home/alex/www/YOURSITE_FOLDER;
  index  index.html index.htm;

  location / {
    try_files $uri $uri/ =404;
  }

  # add other directives here;
}
`;

const code2 = `server {
  listen 80;
  listen [::]:80;
  
  server_name 189.22.208.119;
  
  location / {
    proxy_pass "http://localhost:3000/";
  }
}
`;

const code3 = `server {
  listen 80;
  listen [::]:80;
  
  server_name 185.20.224.109;
  
  location /socket.io/ {
    proxy_http_version 1.1;
    proxy_set_header Upgrade $http_upgrade;
    proxy_set_header Connection "upgrade";
    proxy_pass "http://localhost:3000/socket.io";
  }
  
  location / {
    proxy_pass "http://localhost:3000/";
  }
}
`;

const code4 = `server {
  listen 80;
  listen [::]:80;
  
  server_name ladyworldnas.com;
  return 301 https://$host$request_uri;
}
`;

const code5 = `server {
  listen 443 ssl http2;
  listen [::]:443 ssl http2;
  
  server_name ladyworldnas.com; // 
  root /home/alex/www/PROKA4_STATIC/dist; // 
  index  index.html index.htm; // 
  
  # certs sent to the client in SERVER HELLO are concatenated in ssl_certificate
  
  ssl_certificate /etc/letsencrypt/live/ladyworldnas.com/fullchain.pem; // 
  ssl_certificate_key /etc/letsencrypt/live/ladyworldnas.com/privkey.pem; // 
  
  ssl_session_timeout 1d;
  ssl_session_cache shared:SSL:50m;
  ssl_session_tickets off;
  
  # Diffie-Hellman parameter for DHE ciphersuites, recommended 2048 bits
  # ssl_dhparam /path/to/dhparam.pem; // 
  
  # intermediate configuration. tweak to your needs.
  ssl_protocols TLSv1 TLSv1.1 TLSv1.2;
  ssl_ciphers 'ECDHE-ECDSA-CHACHA20-POLY1305:ECDHE-RSA-CHACHA20-POLY1305:ECDHE-ECDSA-AES128-GCM-SHA256:ECDHE-RSA-AES128-GC$
  ssl_prefer_server_ciphers on;
  
  # HSTS (ngx_http_headers_module is required) (15768000 seconds = 6 months)
  add_header Strict-Transport-Security max-age=15768000;
  
  # OCSP Stapling ---
  # fetch OCSP records from URL in ssl_certificate and cache them
  ssl_stapling on;
  ssl_stapling_verify on;
  
  ## verify chain of trust of OCSP response using Root CA and Intermediate certs
  # ssl_trusted_certificate /path/to/root_CA_cert_plus_intermediates; // 
  
  resolver 8.8.8.8; // 
}
`;

const code6 = `server {
    listen 80;
    listen [::]:80;
    
    server_name ladyworldnas.ru;
    
    return 301 https://ladyworldnas.ru$request_uri;
}

server {
    listen 80;
    listen [::]:80;
    
    server_name www.ladyworldnas.ru;
    
    return 301 https://ladyworldnas.ru$request_uri;
}

server {
    listen 443 ssl http2;
    listen [::]:443 ssl http2;

    gzip on;
    gzip_disable "msie6";
    gzip_types text/plain text/css application/json application/x-javascript text/xml application/xml application/xml+rss text/javascript application/javascript;
    gzip_comp_level 5;

    location /admin {
        index index.html;
        alias /home/alex/www/SITE_ADMIN/build;
        
        if (!-e $request_filename){
            rewrite ^(.*)$ /admin/index.html last;
        }
    }

    location / {
        proxy_pass "http://localhost:3000/";
    }

    # certs sent to the client in SERVER HELLO are concatenated in ssl_certificate
    ssl_certificate /etc/letsencrypt/live/proka4show.ru/fullchain.pem;
    ssl_certificate_key /etc/letsencrypt/live/proka4show.ru/privkey.pem;
    ssl_session_timeout 1d;
    ssl_session_cache shared:SSL:50m;
    ssl_session_tickets off;

    # Diffie-Hellman parameter for DHE ciphersuites, recommended 2048 bits
    # ssl_dhparam /path/to/dhparam.pem;

    # intermediate configuration. tweak to your needs.
    ssl_protocols TLSv1 TLSv1.1 TLSv1.2;
    ssl_ciphers 'ECDHE.......384:DHE-$
    ssl_prefer_server_ciphers on;

    # HSTS (ngx_http_headers_module is required) (15768000 seconds = 6 months)
    add_header Strict-Transport-Security max-age=15768000;

    # OCSP Stapling ---
    # fetch OCSP records from URL in ssl_certificate and cache them
    ssl_stapling on;
    ssl_stapling_verify on;

    ## verify chain of trust of OCSP response using Root CA and Intermediate certs
    # ssl_trusted_certificate /path/to/root_CA_cert_plus_intermediates;

    resolver 8.8.8.8;
}`;

