<CodeBlock>{`const binarySearch = (array, item) => {
  let low = 0;
  let high = array.length - 1;
 
  while(low <= high) {
  	let middle = parseInt((high + low) / 2); // Math.floor((start + end) / 2);
    let value = array[middle];
 
  	if (value === item) {
      return value;
    }
 
    if (value > item) {
      high = middle - 1;
    } else {
      low = middle + 1;
    }
  }
 
  return null;
};
 
const array = [1, 2, 3, 4, 5, 6];
const item = 2;
 
binarySearch(array, item);
`}</CodeBlock>
