<CodeBlock>{`const selectionSort = arr => {
  const arrLength = arr.length;

  for (let i = 0; i < arrLength - 1; i++) {
    let indexMin = i;

    for (let j = i + 1; j < arrLength; j++) {
      if (arr[indexMin] > arr[j]) {
        indexMin = j;
      }
    }

    if (indexMin !== i) { //       -      
      [arr[i], arr[indexMin]] = [arr[indexMin], arr[i]];
    }
  }
  return arr;
};
`}</CodeBlock>
