<CodeBlock>{`const insertionSort = arr => {
    const arrLength = arr.length;

    for (let i = 1; i < arrLength; i++) {
        const current = arr[i];

        let j = i;
        while (j > 0 && arr[j - 1] > current) {
            arr[j] = arr[j - 1];
            j--;
        }
        arr[j] = current;
    }
    return arr;
};
`}</CodeBlock>
