import React, { useLayoutEffect } from 'react';
import { Head, SubHead } from '../components/shared';
import Code from '../components/Code';

const WebworkersPage = () => {
  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <>
      <Head>WEB WORKERS</Head>
      <SubHead></SubHead>
      <p>
        Web worker    -    
         
      </p>
<Code>
{`if (typeof(Worker) !== "undefined") {  
  worker = new Worker("worker.js");
}`}
</Code>
      <p>
          : window  worker.  -    .    
         postMessage   <b>worker.js</b> -        &ensp;
        <b>listener</b>  <b>worker.onmessage</b>        worker.
      </p>
      <p>
                   worker -    
             <b>worker.postMessage()</b>   ,  <b>onmessage</b>   worker.
      </p>
      <p>
            <p>terminate()</p>,    worker
      </p>
<p> <b>index.html</b> (  script)</p>
<Code>
{`var worker;

function testWorker() {
  if (typeof(Worker) !== "undefined") {
    if (typeof(worker) == "undefined") {
      worker = new Worker("worker.js");
    }
    worker.onmessage = function(event) {
      document.getElementById("workerOutput").innerHTML = event.data;
    };
  } else {
    document.getElementById("workerOutput").innerHTML = "Web Workers are not supported in your browser";
  }
}

function terminateWorker() { 
  worker.terminate();
  worker = undefined;
}

function testMainThread() {
  for (var i = 0; i < 200000; i++) { 
    document.getElementById("mainThreadOutput").innerHTML = "Main Thread Counter: " + i;
  }
}
`}
</Code>
<p> <b>worker.js</b></p>
<Code>
{`i = 0;
while (i < 200000) {
  postMessage("Web Worker Counter: " + i);
  i++;
}
`}
</Code>
      <p>
           ,       
          ,    200 000   
           .    #workerOutput  
          .
      </p>
    </>
  )
};

export default WebworkersPage;